@tailwind base;
@tailwind components;
@tailwind utilities;


*, *::after, *::before{
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-family: 'montserrat';
}
*{
    z-index: 10;
}
li.step{
    z-index: unset;
}
