@media screen and (max-width: 650px) {
    .steps [class~=step]{
        min-width: 3rem;
    }
    .steps [class~=step]:before{
        height: .35rem;
    }
    .steps [class~=step]:after{
        width: 1.5rem;    
        height: 1.5rem;    
    }
}

#web3-wallet-address:hover:before, #web3-wallet-address:before{
    left: -5rem;
}
.tooltip-bottom:before{
    left: 0;
}
.tooltip:before{
    background-color: hsl(210, 40%, 80%);
    border: 1px solid hsl(210, 40%, 40%);
}



.navbar .tooltip:hover::before, .navbar .tooltip::before{
    max-width: max-content;
    border: 1px solid #eee;
}
.navbar .tooltip:hover:after, .navbar .tooltip:after{
    display: none;
}
/* .navbar .tooltip:hover:before{
    background-color: red;
    width: 100%;
} */


h1, h2, h3{
    font-family: 'Poppins', 'Montserrat';
}

.active-step::after{
    background-color: rgb(0, 0, 90) !important;
    color: #eee !important;    
}
.steps .step-secondary+.step-secondary:before, .steps .step-secondary:after{
    background-color: white !important;
    color: hsl(240, 100%, 20%) !important;    
}


.custom-btn::before{
    content: '';
    /* background-color: red; */
    height: 50%;
    width: 30%;
    position: absolute;
    top: -3px;
    left: -3px;
    border-left: 5px solid #eee;
    border-top: 5px solid #eee;
}


.trapezoid-buy {
    border-top: 3rem solid #4b5361;
    border-left: 2rem solid transparent;
  }
.trapezoid-sell {
    border-top: 3rem solid #df2f2f;
    border-right: 2rem solid transparent;
  }
  .trapezoid-buy span , .trapezoid-sell span { 
    position: relative;
    top: -2.2rem;
    left: 25%;
    color: white;
    font-weight: bold;
  }
  .trapezoid-sell span{
    left: 38%;
  }
  #market h4.backdrop-blur-sm.bg-opacity-80{
      z-index: 40;
  }
  #market .ar-model{
      /* background: radial-gradient( var(--base), rgba(0, 0, 0, 0.8)); */
      /* background: radial-gradient( #350d49, rgba(0, 0, 0, 0.8)); */
      background: radial-gradient( #005ba0, rgba(0, 0, 0, 0.8));
      border: 2px solid #350d49;
      /* --tw-drop-shadow: drop-shadow(0 0px 8px #cc00ff92); */
      transition: all ;
      transition-duration: 100ms;
      filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
  }
  /* #market .ar-model:hover{
      --tw-drop-shadow: drop-shadow(0px 0px 14px #cc00ff);
  } */
.custom-btn{
    outline: 2px solid #00d8ff;
}
.custom-bg-grad{
    background: linear-gradient(183deg, #ffffff00, black) , linear-gradient(56deg, hsl(0deg 0% 53%), black, black);
}
.custom-btn::after{
    content: '';
    /* background-color: red; */
    height: 50%;
    width: 30%;
    position: absolute;
    right: -3px;
    bottom: -3px;
    border-right: 5px solid #eee;
    border-bottom: 5px solid #eee;
}

.custom-before > *{
    position: relative;
    z-index: 1;
}
.custom-before::after{
    content: '';
    background: red;
    width: max-content;
    height: 100px;
    position: absolute;
    z-index: 0;
}

